import React, {useState, lazy, Suspense} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {
    SettingOutlined,
    UserOutlined,
    LockOutlined,
    HistoryOutlined,FileProtectOutlined

} from "@ant-design/icons";
import Loading from "../../../components/shared-components/Loading";
import {Link, Redirect, Route, Switch} from "react-router-dom";
import InnerAppLayout from "../../../layouts/inner-app-layout";
import LayoutContent from "../../../components/layout-components/LayoutContent";
import Icon from "../../../components/util-components/Icon";
import {Menu} from "antd";

const SettingContent = ({match})=> {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <Route path={`${ match.url }/account`} component={lazy(() => import(`./account/index`))} />
                <Route path={`${ match.url }/notifications`} component={lazy(() => import(`./notifications/index`))} />
                {/*<Redirect to={`${match.url}/account`} from={`${match.url}/`}/>*/}
            </Switch>
        </Suspense>
    )
}
const SettingOption =  ({match, location}) => {
    const [menus,setMenus] = useState([
        {
            key: 'account',
            path: `/account`,
            title: 'Account',
            icon: FileProtectOutlined,
            breadcrumb: false,
            submenu: [
                {
                    key: 'account-profile',
                    path: `/account/profile`,
                    title: 'Profile',
                    icon: FileProtectOutlined,
                    breadcrumb: false,
                    submenu: []
                },
                {
                    key: 'account-change-password',
                    path: `/account/change-password`,
                    title: 'change-password',
                    icon: FileProtectOutlined,
                    breadcrumb: false,
                    submenu: []
                }
            ]
        },
        {
            key: 'notifications',
            path: `/notifications`,
            title: 'Notifications',
            icon: FileProtectOutlined,
            breadcrumb: false,
            submenu: [
                {
                    key: 'notifications',
                    path: `/notifications/default`,
                    title: 'List',
                    icon: FileProtectOutlined,
                    breadcrumb: false,
                    submenu: []
                }
            ]
        },
    ])

    return (
        <Menu
            defaultSelectedKeys={`${match.url}/profile`}
            mode="inline"
            selectedKeys={[location.pathname]}
        >
            {menus.map((menu) => (
                    menu.submenu.length > 0 ? (
                        <Menu.SubMenu
                            key={menu.key}
                            popupClassName="top-nav-menu"
                            title={<span>{menu.icon ? <Icon type={menu?.icon}/> : null}<span>{menu.title}</span></span>}
                        >
                            {
                                menu.submenu.map((subMenuFirst) => (
                                    <Menu.Item key={`${[match.url, subMenuFirst?.path].join("")}`}>
                                        <Icon type={subMenuFirst?.icon} className="text-primary font-size-md"/>
                                        <span>{subMenuFirst?.title}</span>
                                        <Link to={`${[match.url, subMenuFirst?.path].join("")}`}/>
                                    </Menu.Item>
                                ))
                            }

                        </Menu.SubMenu>
                    ) : (
                        <Menu.Item key={`${[match.url, menu?.path].join("")}`}>
                            <Icon type={menu?.icon} className="text-primary font-size-md"/>
                            <span>{menu?.title}</span>
                            <Link to={`${[match.url, menu?.path].join("")}`}/>
                        </Menu.Item>
                    )
                )
            )}
        </Menu>
    )
}
const SettingsViews = (props)=> {
    let {match} = props
    return (
        <LayoutContent style={{marginTop: 140, minHeight: "calc(100vh - 240px)"}}>

            <div className="w-full max-w-[1280px] mx-auto py-10 px-4">
                <InnerAppLayout
                    sideContentWidth={320}
                    sideContent={<SettingOption match={match} {...props}/>}
                    mainContent={<SettingContent {...props}/>}
                />
            </div>
        </LayoutContent>

    )
}

SettingsViews.propTypes = {}
SettingsViews.defaultProps = {}

export default connect(()=> {
    return {}
},{})(SettingsViews)
